body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.head{
  background-color: black;
  color: white;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.head h3,i{
  margin-left: 10px;
  margin-right: 30px;
}
.cart i{
 
  color: white;
}
.cart-item{
  border-radius: 50%;
  position: absolute;
  display: flex;
  align-items: center;
  justify-content: center;
  width: 15px;
  height: 15px;
  background-color: blue;
  font-size: 10px;
  top: 10px;
  right: 20px;
}
.Items{
  display: inline-flex;
  padding: 10px;
  margin: 4px;
  gap: 20px;
  border: 1px solid;
  border-radius: 10px;
  box-shadow: 0px 0px 7px black;

}
.Items img{
  width: 150px;
  height: 180px;
  
}
.Items div{
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
}
.ItemsContainer{
      display: grid;
      grid-template-columns: auto auto auto;
      grid-auto-flow:dense;
      gap: 10px;
      margin: 10px;
      /* display: flex;
      justify-content:space-around;

      align-items: center; */
}
.Items button :not(:nth-child(3)){
    background-color: blue;
    color: white;
    border: none;
    border-radius: 5px;
    height: 25px;
    width: 25px;
    margin: 3px;
    transition: 0.5s;
}
.Items button:hover:not(:nth-child(3)){
  background-color: green;
}
footer{
  margin: 0;
  bottom: 0;
  right: 0;
  width: 100%;
  position: absolute;
  display: flex;
  align-items: flex-end;
  background-color: rgb(106, 110, 113);
  color: white;
  justify-content: right;
  font-size: 20px;
}
footer p{
  margin-right: 30px;
  word-spacing: 2px;
}
.btn1{
  background-color: blue;
  border: none;
  color: white;
    
    border-radius: 5px;
    padding: 5px;
    transition: 0.5s;
}



